* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

li {
    list-style: none;
}

a {
    color: inherit;
    text-decoration: none;
}

body {
    color: #333;
    line-height: 1;
}

.header {
    height: 100px;
}

.header .inner {
    max-width: 1280px;
    width: calc(100% - 30px);
    height: 100%;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.header .inner .nav {}
.header .inner .nav .gnb {
    display: flex;
    justify-content: space-between;
}
.header .inner .nav .gnb > li {
    padding: 0 20px;
}
.header .inner .nav_open {
    width: 20px;
    position: relative;
    z-index: 99;
}
.header .inner .nav_open img {
    width: 100%;
}

.all_nav {
    width: 50%;
    height: 100vh;
    position: fixed;
    top: 0;
    right: -100%;
    background-color: rgb(89, 138, 180);
    color: white;
    transition: 0.6s;
    padding: 50px;
}

.all_nav .gnb > li {
    margin-bottom: 15px;
}

.all_nav.on {
    right: 0;
    transition: 0.6s;
}

.input_box {}
.input_box input {
    width: 300px;
    height: 30px;
    border-radius: 25px;
    border: 1px solid ivory;
    padding: 5px 15px;
}
.input_box .text {
    margin-top: 15px;
}
.input_box .text span {
    color: ivory;
}

.all_nav .input_box {
    margin-top: 20px;
}